/* global console, document, Excel, Office */

// The initialize function must be run each time a new page is loaded
import RequestContext = Excel.RequestContext;

Office.onReady((): void => {
  const lAppBody: HTMLElement | null = document?.getElementById("app-body");
  const lRunButton: HTMLElement | null = document?.getElementById("runButton");

  if (lAppBody) {
    lAppBody.style.display = "flex";
  }

  if (lRunButton) {
    lRunButton.onclick = onButtonClick;
  }
  console.log("Office.onReady");
});

async function loadSetting(): Promise<string> {
  let mode = await OfficeRuntime.storage.getItem("orientationMode");

  if (!mode) {
    mode = "row"; // Значение по умолчанию
    await OfficeRuntime.storage.setItem("orientationMode", mode); // Сохранить его сразу
  }

  console.log("Загруженное значение:", mode);
  return mode;
}

document.addEventListener("DOMContentLoaded", async (): Promise<void> => {
  console.log("DOMContentLoaded");
  const rowOption = document.getElementById("rowOption") as HTMLInputElement;
  const columnOption = document.getElementById("columnOption") as HTMLInputElement;

  // Загрузка сохранённого значения
  // const saved = OfficeRuntime.storage.getItem("orientationMode");
  const saved = await loadSetting();

  console.log("TASKPANE Режим из storage:", saved);
  if (saved === "column") {
    columnOption.checked = true;
  } else {
    rowOption.checked = true;
  }

  // Обработчики событий
  rowOption.addEventListener("change", async (): Promise<void> => {
    onRadioButtonChange(rowOption);
  });

  columnOption.addEventListener("change", async (): Promise<void> => {
    onRadioButtonChange(columnOption);
  });
});

export async function onRadioButtonChange(aHTMLInputElement: HTMLInputElement): Promise<void> {
  if (aHTMLInputElement.checked) {
    saveSetting(aHTMLInputElement.value as "row" | "column");
  }

  // пример пересчета Excel
  await Excel.run(async (context: RequestContext): Promise<void> => {
    context.workbook.application.calculate(Excel.CalculationType.full);
    await context.sync();
  });
}

